@import '../../variables.scss';
@import '../../mixin.scss';


.home { 
  dl, dd, dt, h1, h2, h3, h4, ul, li {
    margin: 0;
    padding:0;
  }
  .moduleWrap {
    .title {
      color: $DarkBlack;
      font-size: 36px;
      line-height: 1.2;
      @include Barlow;
      font-weight: 700;
      text-align: center;
      margin-bottom: 24px;
      margin-top: 160px;
    }

    .description {
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      margin: 0 auto;
      max-width: 841px;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.2;
      text-align: center;
      margin-bottom: 24px;
      color: $DarkenGrey;
      >span {
        font-weight: 400;
        font-size: 16px;
        line-height: 1.2;
        color: $DarkenGrey;
      }
      >a {
        margin-left: 4px;
        display: flex;
        flex-direction: row;
        align-items: center;
        text-decoration: none;
        transition: transform .4s ease;
        &:hover {
          transform: translateY(-2px);
        }
        >span {
          color: $Green;
          font-weight: 500;
          line-height: 1.2;
          font-size: 16px;
        }
        >img {
          width: 16px;
          height: 16px;
          margin-left: 4px;
        }
      }
    }
    .tilesContainer {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  .sloganWrap {
    background: $BgBlack;
    .inner {
      @include inner;
      overflow: hidden;
      width: 100%;
      background: url('../../images/freshBank/banner.png') no-repeat right center;
      // background-size: 522px 546px;
      background-size: 595px 595px;
      // height: 546px;
      height: 595px;
      display:flex;
      flex-direction: column;
      justify-content: center;
      .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        .title {
          @include Barlow;
          font-size: 54px;
          color: $PureWhite;
          line-height: 1.2;
          font-weight: 700;
          max-width: 613px;
        }
        .description {
          max-width: 610px;
          margin-top: 22px;
          @include Barlow;
          font-size: 16px;
          line-height: 1.5;
          color: $PureWhite;
          font-weight: 400;
        }
        .launchApp {
          margin-top: 27px;
          @include greenBtn;
        }
      }
    }
  }

  .news {
    .newsContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      .title {
        margin-top: 100px;
        margin-bottom: 48px;
      }
      .newsSwiper {
        .newItem {
          width: 100%;
          text-decoration: none;
          .imgWrap {
            position: relative;
            padding-bottom: 57.29166666666666%;
          }
          .newPic {
            position:absolute;
            left:0;
            top:0;
            width: 100%;
            height: 100%;
            border-radius: 16px;
          }
          .newTitle {
            margin-top: 20px;
            font-weight: 700;
            font-size: 24px;
            line-height: 1.2;
            color: $DarkBlack;
            height: 58px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;    
          }
          .newTime {
            position: relative;
            margin-top: 16px;
            padding-top: 20px;
            font-weight: 400;
            font-size: 20px;
            line-height: 1.2;
            color: $DarkenGrey;
            &:before {
              content: '';
              width: 33px;
              height: 4px;
              position: absolute;
              top: 0;
              left: 0;
              background: $Green;
            }
          }
        }
      }
    }
  }

  .supplyEarn {
    .supplyContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      .supplyEarnSwiper {
        margin-top: -16px;
        :global {
          .swiper-wrapper {
            align-items: stretch;
          }
          .swiper-slide {
            height: initial;
            margin-bottom: 16px;
            margin-top: 16px;
          }
        }
        .upplyEarnItem {
          height: 100%;
          max-width: 380px;
          background: $PureWhite;
          padding: 30px;
          border-radius: 16px;
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          align-items: stretch;
          cursor: pointer;
          transition: all ease .4s;
          box-shadow: 0 3px 6px rgba($DarkBlack, 0);
          transform: translateY(0);
          &:hover {
            box-shadow: 0 8px 16px rgba($DarkBlack, .1);
            transform: translateY(-6px);
          }
          dt {
            margin-top: 16px;
            @include Barlow;
            font-size: 24px;
            font-weight: 700;
            line-height: 28.8px;
            color: $DarkBlack;
          }
          dd {
            margin-top: 8px;
            @include Barlow;
            font-size: 14px;
            font-weight: 400;
            line-height: 24px;
            color: $DarkenGrey;
          }
        }
      }
    }
  }

  .marketData {
      .marketDataContainer {
          padding-top: 100px;
      }
  }



  .features {
    .featuresContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      .featuresSwiper {
        :global {
          .swiper-wrapper {
            align-items: stretch;
          }
          .swiper-slide {
            height: initial;
          }
        }
        .featuresItem {
          height: 100%;
          max-width: 380px;
          background: $PureWhite;
          padding: 55px 36px;
          border-radius: 16px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: stretch;
          img {
            align-self: center;
          }
          dt {
            margin-top: 30px;
            font-size: 24px;
            font-weight: 700;
            line-height: 28.8px;
            color: $DarkBlack;
            width:100%;
            text-align: center;
          }
          dd {
            margin-top: 12px;
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
            width:100%;
            text-align: center;
            color: $DarkenGrey;
          }
        }
      }
    }

  }


  .partners {
      margin-bottom: 100px;
      .partnersContainer {
          display: flex;
          flex-direction: column;
          align-items: center;
          .tilesContainer {
              display: flex;
              flex-direction: row;
              flex-wrap: wrap;
              justify-content: center;
              max-width: 1120px;
              .tile {
                  margin: 10px;
                  background-color: $PureWhite;
                  border-radius: 8px;
                  height: 114px;
                  width: 260px;
                  padding: 20px;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  cursor: pointer;
                  transition: all ease .4s;
                  box-shadow: 0 3px 6px rgba($DarkBlack, 0);
                  transform: translateY(0);
                  &.tileNoHref {
                    cursor: default;
                  }
                  &:hover {
                      box-shadow: 0 8px 16px rgba($DarkBlack, .3);
                      transform: translateY(-6px);
                  }
              }
          }
      }
  }

  .roadmap {
    .roadmapContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      .roadmapWrap {
        margin-top: 50px;
        width: 100%;
        .lineCircle {
          position: relative;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          width: 100%;
          padding: 0 25%;
          .roadmapLine {
            position: absolute;
            left: 0;
            top: 6px;
            width: 100%;
            height: 5px;
            background: linear-gradient(to right, transparent 0%, $LGrey 31.77%, $LGrey 68.23%, transparent 100%);
          }
          .roadmapCircle {
            position: relative;
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 21px;
            height: 21px;
            border-radius: 50%;
            background: $Green;
            border: 4px solid $Bg;
            &:nth-of-type(4),
            &:nth-of-type(5) {
              background: $Blue;
            }
          }
        }
        .roadmapList {
          position: relative;
          display: flex;
          justify-content:space-between;
          flex-direction: row;
          // background: rgba(255, 0, 0, .2);

          .arrowBottom {
            display: flex;
            width: 50%;
            margin-bottom: 20px;
            // outline: 1px solid red;
            &:nth-of-type(1) {
              padding-left: 25%;
              .roadmapItem {
                transform: translate(calc(-50% + 10px), 0);
              }
            }
            &:nth-of-type(2) {
              padding-left: 8%;
              .roadmapItem {
                transform: translate(-50%, 0);
              }
            }
            .roadmapItem {
              &::after {
                content: '';
                position: absolute;
                bottom: -20px;
                left: 50%;
                transform: translateX(-50%);
                border: 10px solid $PureWhite;
                border-bottom: 10px solid transparent;
                border-right: 10px solid transparent;
                border-left: 10px solid transparent;
              }
            }
          }

          .arrowTop {
            display: flex;
            margin-top: 20px;
            // outline: 1px solid red;
            &:nth-of-type(1) {
              width: 74%;
              padding-left: 42%;
              .roadmapItem {
                transform: translate(-50%, 0);
              }
            }
            &:nth-of-type(2) {
              width: 26%;
              .roadmapItem {
                transform: translate(-50%, 0);
              }
            }
            .roadmapItem {
              &::after {
                content: '';
                position: absolute;
                top: -20px;
                left: 50%;
                transform: translateX(-50%);
                border: 10px solid $PureWhite;
                border-top: 10px solid transparent;
                border-right: 10px solid transparent;
                border-left: 10px solid transparent;
              }
            }
          }
        }

      }

      .roadmapItem {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        background: $PureWhite;
        border-radius: 24px;
        min-height: 176px;
        max-width: 380px;
        .itemHd {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          background: $Green;
          height: 100%;
          width: 80px;
          border-radius: 24px;
          .year {
            background: $PureWhite;
            font-weight: 700;
            font-size: 20px;
            color: $Green;
            width: 64px;
            height: 32px;
            line-height: 32px;
            text-align: center;
            border-radius: 16px;
          }
          .quarter {
            font-family: Cabin;
            color: $PureWhite;
            margin-top: 26px;
            font-weight: 500;
            font-size: 40px;
            line-height: 1;
          }
        }
        .itemBd {
          flex: 1 0 80px;
          list-style: none;
          padding: 30px 24px;
          li {
            position: relative;
            font-size: 16px;
            font-weight: 400;
            color: $DarkBlack;
            padding-left: 16px;
            line-height: 1.5;
            padding-bottom: 20px;
            &:last-child {
              padding-bottom: 0;
            }
            &::before {
              content: '';
              position: absolute;
              left:0;
              top: 8px;
              width: 8px;
              height: 8px;
              background: $Green;
              border-radius: 50%;
            }
          }
        }

        &.roadmapItemBlue {
          .itemHd {
            background: $Blue;
            .year {
              color: $Blue;
            }
          }
          .itemBd {
            li {
              &::before {
                background: $Blue;
              }
            }
          }
        }
      }

      .roadmapSwiper {
        :global {
          .swiper-slide {
            display: flex;
            height: initial;
          }
        }
        .roadmapItem {
          width: 100%;
        }
      }
    }
  }


  .community {
    .communityContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        .description {
          margin-bottom: 12px;
        }
        .tilesContainer {
          .tileWrap {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 12px;
            width: 120px;
            height: 140px;
            padding-top: 38px;
            padding-bottom: 20px;
            border-radius: 12px;
            transition: all ease .4s;
            cursor: pointer;
            &:nth-of-type(1) {
              background-color: #37B7FF;
            }
            &:nth-of-type(2) {
              background-color: #806BFF;
            }
            &:nth-of-type(3) {
              background-color: #37B7FF;
            }
            &:nth-of-type(4) {
              background-color: #313131;
            }
            &:hover {
              transform: translateY(-10px);
            }
            .tile {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                text-decoration: none;
                .name {
                  margin-top: 10px;
                  color: $PureWhite;
                  font-size: 16px;
                  font-weight: 500;
                  line-height: 24px;
                  text-decoration: none;
                }
            }
          }

        }
    }
  }
}



@media (max-width: 991.98px) { 
  .home {
    .moduleWrap {
      .title {
        margin-top: 80px;
      }
    }
    .sloganWrap {
      .inner {
        height: 744px;
        padding-left: 36px;
        padding-right: 36px;
        justify-content: flex-start;
        background-size: 100%;
        background-position: center bottom;
        .content {
          margin-top: 30px;
          .title {
            font-size: 36px;
            line-height: 1.2;
          }
          .description {
            margin-top: 16px;
            line-height: 1.714;
          }
        }
        .launchApp {
          margin-top: 24px;
        }
      }
    }

    .news {
      .newsContainer {
        padding-left: 0;
        padding-right: 0;
        max-width: 1200px;
        .title {
          margin-top: 64px;
          padding-left: 16px;
          padding-right: 16px;
        }
      }
    }

    .supplyEarn {
      .supplyContainer {
        padding-left: 0;
        padding-right: 0;
        max-width: 1200px;
        .title {
          padding-left: 24px;
          padding-right: 24px;
        }
        .description {
          flex-direction: column;
          align-items: center;
          >a {
            margin-left: 0;
            margin-top: 10px;
          }
        }
        .supplyEarnSwiper {
          .upplyEarnItem {
            max-width: initial;
          }
        }
      }
    }

    .marketData {
      .marketDataContainer {
        padding-top: 48px;
      }
    }


    .features {
      .featuresContainer {
        max-width: 1200px;
        padding-left: 0;
        padding-right: 0;
        title {
          padding-left: 16px;
          padding-right: 16px;
        }
        .description {
          padding-left: 44px;
          padding-right: 44px;
        }
        .featuresSwiper {
          .featuresItem {
            max-width: initial;
          }
        }
      }
    }

    .partners {
      .partnersContainer {
        .title {
          margin-top: 64px;
        }
        .description {
          padding-left: 28px;
          padding-right: 28px;
        }
        .tilesContainer {
          flex-direction: column;
          max-width: initial;
          width: 100%;
          padding-left: 12px;
          padding-right: 12px;
          .tile {
            width: 100%;
            margin: 0;
            margin-bottom: 20px;
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
      }
    }
    
    
    .community {
      .communityContainer {
        .title {
          margin-top: 100px;
        }
        .description {
          padding-left: 28px;
          padding-right: 28px;
        }
        .tilesContainer {
          margin-top: 22px;
          .tileWrap {
            margin: 10px;
          }
        }
      }
    }

    .roadmap {
      .roadmapContainer {
        padding-left: 0;
        padding-right: 0;
        .title {
          padding-left: 16px;
          padding-right: 16px;
        }
        .description {
          padding-left: 44px;
          padding-right: 44px;
        }
        .roadmapItem {
          max-width: initial;

        }
      }
    }




    .homeSwiper {
      :global {
        .swiper-slide {
          padding-left: 16px;
          &:last-child {
            padding-right: 16px;
          }
        }
      }
    }

  }
}


